*{
    margin: 0;
    padding: 0;
}

.bg{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    z-index: -10;
    opacity: 0.4;
    img{
        object-fit: cover;
        height: 100%;
        width: 100%;
    }
}

body {
   background-color: black;
}

.hero {
    display: flex;
    padding: 0 80px 0px 120px;
    background-color: black;
    //border-bottom: 3px solid rgb(226, 69, 108);
    img {
       object-fit: cover;
        height: 200px;
        border-radius: 15px;
    }
}

.hero-title {
    padding-top: 50px;
    padding-bottom: 20px;
    flex-shrink: 0;
    margin-right: 15%;
    margin-bottom: 50px;
    margin-top: 150px;
    h1, h2 {
        color: white;
        font-size: 50px;
    }
    
    h3 {
        color: white;
        font-size: 28px;
    }
}



    h1 {
            animation: 0.5s title forwards ease-in-out;
        }

#freelance, #dev {
    animation: 0.5s title forwards ease-in-out;
    opacity: 0;
}
#freelance {
    animation-delay: 0.3s;
    
}
#dev {
    animation-delay: 0.6s;
 
}

.mb {
    display: flex;
    align-items: center;
    padding: 20px;
    margin-top: 200px;
    width: 100%;
    z-index: 999;
    span{
       color: white;
        font-size: 80px;
        animation: 20s color-mb4 infinite;
    }
}

#mb1, #mb2, #mb3  {
    opacity: 0;
    animation: 1s anim-mb forwards;
}

#mb4, #mb5, #mb6 {
    opacity: 0;
    animation: 20s anim-dev infinite ease-in-out;
}




#mb1{
    animation-delay: 1s;
}


#mb2{
    animation-delay: 1.2s;
}
#mb3{
    animation-delay: 1.4s;
}
#mb4{
    animation-delay: 1.6s;
  
}
#mb5{
    animation-delay: 1.8s;
   
    
}
#mb6{
    animation-delay: 2s;
   
}

#circle{
  
    display: flex;
    align-items: center;
    justify-content: center;
    height: 150px;
    width: 150px;
    border-top: 2px solid transparent;
    border-left: 2px solid transparent;
    border-bottom: 2px solid transparent;
    border-radius: 50%;
    animation: 20s circle-anim infinite;
    position: relative;
    left: 135px;
  
}

.trait{
    margin: 20px 0 20px 0;
    border-bottom: 2px solid transparent;
  
   
    transform: scale(0);
    animation: 12s scale-trait infinite;
    animation-delay: 1s;
    transform-origin: 0;
}

@keyframes scale-trait {
    0%{
        transform: scale(0);
        border-bottom: 2px solid rgb(7, 107, 238);
    }
    
    10%{
        transform: scale(1);
        border-bottom: 2px solid rgb(7, 107, 238);
    }
    20%{
        transform: scale(1);
        border-bottom: 2px solid rgb(7, 107, 238);
    }
    25%{
        opacity: 0;
        transform: scale(0);
    }
    35%{
        opacity: 1;
        transform: scale(1);
        border-bottom: 2px solid rgb(226, 69, 108);
    }
    45%{
        transform: scale(1);
        border-bottom: 2px solid rgb(226, 69, 108);
    }
    50%{
        opacity: 0;
        transform: scale(0);
    }

    100%{
        border-bottom: 2px solid rgb(238, 255, 0);
        transform: scaleX(1);
    }
}

@keyframes circle-anim{
    0%{
        border-left: 2px solid rgb(226, 69, 108);
        border-top: 2px solid rgb(238, 255, 0);
        border-bottom: 2px solid rgb(7, 107, 238);
        transform: rotate(0) ;
       
       
    }
    45% {
        opacity: 1;
        transform:  rotate(0)  ;
        border-left: 2px solid rgb(226, 69, 108);
        border-top: 2px solid rgb(238, 255, 0);
        border-bottom: 2px solid rgb(7, 107, 238);
        
      
    }
    50%{
        transform: rotate(-360deg) scale(2);
        border-left: 2px solid rgb(226, 69, 108);
        border-top: 2px solid rgb(238, 255, 0);
        border-bottom: 2px solid rgb(7, 107, 238);
       
    }
    95%{
        transform: rotate(-360deg) scale(2);
        border-left: 2px solid rgb(226, 69, 108);
        border-top: 2px solid rgb(238, 255, 0);
        border-bottom: 2px solid rgb(7, 107, 238);
      
    }


    100% {
      
        transform: rotate(0deg) ;
        border-left: 2px solid rgb(226, 69, 108);
        border-top: 2px solid rgb(238, 255, 0);
        border-bottom: 2px solid rgb(7, 107, 238);
    }
}

@keyframes anim-mb {
    0%{
      
       opacity: 0;
    }
  
    100%{
      color: rgb(255, 255, 255);
        opacity: 1;
    }
}

@keyframes anim-dev {
    0% {
        transform: translateY(-200px);
    }
    2% {
        transform: translateY(60px);
      
        opacity: 1;
    }
    5%{
        transform: translateY(-30px);
    }
    10% {
        transform: translateY(20px);
    }
    15%{
        transform: translateY(-5px);
    }
    20%{
       
        transform: translateY(0px);
    }

    70%{
       
        transform: translateY(0px);
    }
    100%{
        opacity: 1;
        transform: translateY(0px);
    }
}

#img-hero{
    
    animation: 10s img-hero infinite;
    display: flex;
    justify-content: center;
    background-color: black;
    padding-bottom: 80px;
    img{
        opacity: 0;
        object-fit: cover;
        width: 40%;
        min-width: 300px;
        animation: 8s show-img forwards;
      //  margin-bottom: 80px;
     
    }
   
}

@keyframes show-img{
    0%{
        opacity: 0;
     
       // transform: translateX(-1000px);
    }
    100%{
        opacity: 1;
       // transform: translateX(0px);
    }
}

@keyframes img-hero{
    0% {
        border-top: 3px solid rgb(226, 69, 108);
        border-bottom: 5px solid rgb(226, 69, 108);
    }
    33%{
        border-top: 3px solid rgb(238, 255, 0);
        border-bottom: 5px solid rgb(226, 69, 108);
    }
    67%{
        border-top: 3px solid rgb(7, 107, 238);
        border-bottom: 5px solid rgb(226, 69, 108);
    }
    100%{
        border-top: 3px solid rgb(226, 69, 108);
        border-bottom: 5px solid rgb(226, 69, 108);
    }
   
}



nav {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    justify-content: flex-end;
    z-index: 3;
    width: 100%;
    padding: 0 50px 0 50px;
    background-color: rgb(0, 0, 0);
    a {
        text-decoration: none;
        color: white;
        padding: 20px;
        margin: 0 5px 5px 5px;
        font-size: 20px;
        border-top: 2px solid transparent;
        transition: 0.5s;
        &:hover {
            color: rgb(226, 69, 108);
            border-top: 2px solid rgb(226, 69, 108);
        }
    }
}


@keyframes title {
    0% {
        transform: translateX(-600px);
        opacity: 0;
    }
  
   
    50% {
        transform: translateX(40px);
        opacity: 1;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

.services, .offres, .realisations {
    h3 {
        color: rgb(0, 0, 0);
        margin-left: 90px;
        padding: 30px;
        font-size: 28px;
    }
}

/// services *****************************************************
/// 
/// 

#serv{
    margin-top: 100px;
    h3{
        color: white;
        padding-top: 100px;
    }
}


.group-card{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin: 0 120px 0 120px;
}

.card-service{
    border-radius: 8px;
    background: rgb(255, 255, 255);
    color: rgb(3, 3, 3);
    box-shadow: 2px 2px 2px 2px rgb(196, 193, 193);
    padding: 20px;
    animation: 2s show-card forwards;
    h4{
        margin-bottom: 10px;
        padding-bottom: 4px;
        border-bottom: 1px solid white;
    }
}

@keyframes show-card {
    0%{
        opacity: 0;
        transform: translateY(50px);
    }
    100%{
        opacity:1;
        transform: translateY(0);
    }
}

/// offres *****************************************************
/// 
#of{
margin-top: 100px;
h3{
    color: white;
    padding-top: 100px;
}
}

.group-offres{
display: grid;
grid-template-columns: 1fr 1fr 1fr;
gap: 30px;
margin: 0 120px 0 120px;
}

.card-offre{
    height: 250px;
    border: 1px solid grey;

}

/// realisations *****************************************************
#rea{
    margin-top: 100px;
    h3{
        color: white;
        padding-top: 100px;
    }
}
.group-rea{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin: 0 120px 0 120px;
}

.card-rea{
    height: 250px;
    border: 1px solid grey;
    border-radius: 10px;
    transform: scale(0);
    transition: 1s;
   
    
}

.showCard-rea{
    height: 250px;
    border: 1px solid grey;
    border-radius: 10px;
    transform: scale(1);
    transition: 0.7s;
    cursor: pointer;
    &:hover{
        transform: scale(1.05);
        box-shadow: 3px 3px  rgb(214, 214, 214);
    }
}




footer{
    height: 200px;
    background: black;
    margin-top: 150px;
}